.page {
  &__underTitle {
    margin: 0;
  }
}

.Homepage {
  padding: 48px 60px;

  &__image {
    width: 620px;
    height: 190px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 48px;
  }

  &__footerImage {
    width: 340px;
    height: 290px;
  }

  &__buttons {
    display: flex;
    gap: 20px;
    width: 300px;
    height: 44px;
  }
}
